:host
  display: block

.custom-cursor-container
  position: relative

.hidden-cursor-span
  position: absolute
  visibility: hidden
  white-space: pre 

.custom-cursor-input
  position: relative
  caret-color: transparent 
  width: 100%
  padding: 6px 16px
  border: 1px solid #ccc
  border-radius: 4px
  font-size: 16px

.custom-caret
  position: absolute
  top: 8px 
  left: 16px 
  width: 2px 
  height: 1.3em 
  background-color: black 
  animation: blink 1s linear infinite 
  pointer-events: none 
  z-index: 1 

@keyframes blink
  0%
    opacity: 1
  50%
    opacity: 0
  100%
    opacity: 1

.send-icon
  position: absolute
  right: 16px
  top: 7px
  transform: translateY(-50%) scale(0)
  opacity: 0
  pointer-events: none

  svg
    width: 24px
    height: 24px


@keyframes popIn
  0%
    transform: scale(0)
    opacity: 0
  70%
    transform: scale(1.05)
    opacity: 1
  100%
    transform: scale(1)
    opacity: 1

@keyframes popOut
  0%
    transform: scale(1)
    opacity: 1
  100%
    transform: scale(0)
    opacity: 0